<script>
	import {computed, reactive, toRefs, ref, onUpdated, nextTick} from 'vue'
	import { useStore } from 'vuex'
    import axios from 'axios'
	export default{
		name: 'TablePost',
		async setup(){
			const data = reactive({
			    data:[]
			});
		    // ambil post dari database dengan id sesuai dengan id
			const store = useStore();
			store.dispatch('getAllPost')
		    const result = await axios.get(`https://script.google.com/macros/s/AKfycbwo_TI09KsJDIe9SS0d9UcJNe-l49p5U5QHs2yYhKpidablaHEAKnToilY1_5PjcROG/exec?action=read&table=post`)
		    .then(res => {
		      return res.data.data
		    })
		    .catch(err => {
		      console.log(err)
		    })
		    const dataPost = computed(()=>{
		    // console.log(store.state.post);

		      return store.getters.getAllPost[0];
		    })

		  // timpa data dengan result
		    console.log(dataPost);
		    data.data = result
		    // data.title = result.title;
		    // data.author = result.author;
		    // data.body = result.body;


		    const hapus =  (id)=>{
		    	let text = "apakah anda yakin ingin menghapus?";
		    	if (confirm(text) == true){
			    	axios.get(`https://script.google.com/macros/s/AKfycbzb_O-KWooBNQw0Hq8xuQPT0l0Gn3kDZRUPA7hKEJ3ue6FCVZMdp3tBtwynMGcAopBV/exec?action=delete&table=post&id=${id}`)
				    .then(res => {
				      console.log(res);
				      alert('Berhasil diHapus')
					  store.dispatch('getAllPost')
				    })
				    .catch(err => {
				    	alert('gagal dihapus!')
				      console.log(err)
				    })
			    }


		    }
		    return {...toRefs(data),hapus};

		}
	}
</script>
<template>
    <CTableRow v-for="item in data" :key="item.name">
		<CTableDataCell class="text-center">
		  {{item.id}}
		</CTableDataCell>
		<CTableDataCell>
		  <div>{{ item.title }}</div>
		  <div class="small text-medium-emphasis">  {{ item.timestamp}}
		  </div>
		</CTableDataCell>
		<CTableDataCell class="text-center">
		  {{item.author}}
		</CTableDataCell>
		<CTableDataCell>
		  <strong>
		    {{ item.Category}}
		  </strong>
		</CTableDataCell>
		<CTableDataCell>
		  <div class="small text-medium-emphasis">Last login</div>
		  <strong>
		  {{ item.timestamp }}
		  </strong>
		</CTableDataCell>
		<CTableDataCell class="text-center">
		  <button>Edit</button>
		  <button @click="hapus(item.id)">Hapus</button>        
		</CTableDataCell>
	</CTableRow>
</template>

